<template>
  <v-card width="600" class="mx-auto mt-15">
    <v-row>
      <v-spacer></v-spacer>
      <v-img
        class="mt-10 mb-5"
        lazy-src="https://bcas.lk/wp-content/themes/Divi/images/logo.png"
        max-height="150"
        max-width="250"
        src="https://bcas.lk/wp-content/themes/Divi/images/logo.png"
      ></v-img>
      <v-spacer></v-spacer>
    </v-row>
    <v-divider></v-divider>
    <v-card-text>
      <v-form>
        <v-alert outlined type="error" border="left" v-if="this.has_error">
          Reset password failed. The link you followed has expired.
        </v-alert>
        <v-text-field
          label="Email"
          prepend-icon="mdi-account-circle"
          v-model="email"
        />

        <v-text-field
          :type="showPassword ? 'text' : 'password'"
          label="Password"
          v-model="password"
          prepend-icon="mdi-lock"
        />

        <v-text-field
          :type="showPassword ? 'text' : 'password'"
          label="Confirm Password"
          v-model="password_confirmation"
          prepend-icon="mdi-lock"
          :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
          @click:append="showPassword = !showPassword"
        />
      </v-form>
    </v-card-text>
    <v-divider></v-divider>
    <v-card-actions>
      <v-spacer></v-spacer>
      <span class="mr-5">
        <router-link to="/login">
          Back to login
        </router-link>
      </span>
      <v-btn color="info" @click="resetPassword">Reset Password</v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
import axios from "axios";
export default {
  name: "ResetPasswordForm",
  data() {
    return {
      token: null,
      email: null,
      password: null,
      password_confirmation: null,
      has_error: false,
      showPassword: false
    };
  },
  methods: {
    resetPassword() {
      axios
        .post("/auth/reset/password/", {
          token: this.$route.params.token,
          email: this.email,
          password: this.password,
          password_confirmation: this.password_confirmation
        })
        .then(
          () => {
            this.$router.push("/login");
          },
          () => {
            this.has_error = true;
          }
        );
    }
  }
};
</script>
